import { Component, Renderer2, OnInit, AfterViewInit, ViewChild } from '@angular/core';

@Component({
  selector: 'app-flashcard',
  templateUrl: './flashcard.component.html',
  styleUrls: ['./flashcard.component.scss']
})
export class FlashcardComponent implements OnInit, AfterViewInit {

  @ViewChild('card') flashcard;

  constructor(private renderer: Renderer2) { }

  ngOnInit() {
  }

  ngAfterViewInit() {
  }

  public flipCard($event: any) {
    if (this.flashcard.nativeElement.style.transform == "rotateY(180deg)") {
      this.flashcard.nativeElement.style.transform = "rotateY(0deg)";
    }
    else {
      this.flashcard.nativeElement.style.transform = "rotateY(180deg)";
    }
  }

}



