import { BrowserModule } from '@angular/platform-browser';
import { BrowserAnimationsModule } from '@angular/platform-browser/animations';
import { FlexLayoutModule } from "@angular/flex-layout";
import { FormsModule } from '@angular/forms';
import { NgModule } from '@angular/core';
import { AppRoutingModule } from './app-routing.module';

import { AppComponent } from './app.component';

/* Angular Material Modules */
import {
  MatButtonModule, MatToolbarModule, MatMenuModule, MatIconModule, MatInputModule, MatCardModule,
  MatListModule, MatProgressBarModule
} from '@angular/material';

import { HomeComponent } from './components/home/home.component';
import { NavbarComponent } from './components/navbar/navbar.component';
import { FlashcardsComponent } from './components/flashcards/flashcards.component';
import { FlashcardComponent } from './components/flashcard/flashcard.component';
import { FlashcardsSidenavComponent } from './components/flashcards-sidenav/flashcards-sidenav.component';

@NgModule({
  declarations: [
    AppComponent,
    HomeComponent,
    NavbarComponent,
    FlashcardsComponent,
    FlashcardComponent,
    FlashcardsSidenavComponent
  ],
  imports: [
    BrowserModule,
    BrowserAnimationsModule,
    FlexLayoutModule,
    FormsModule,
    AppRoutingModule,
    MatButtonModule, MatToolbarModule, MatMenuModule, MatIconModule, MatInputModule, MatCardModule,
    MatListModule, MatProgressBarModule
  ],
  providers: [],
  bootstrap: [AppComponent]
})
export class AppModule { }



