import { Component, OnInit, Output, AfterContentInit, ContentChild, AfterViewInit, ViewChild, ViewChildren } from '@angular/core';

@Component({
  selector: 'app-navbar',
  templateUrl: './navbar.component.html',
  styleUrls: ['./navbar.component.scss']
})
export class NavbarComponent implements OnInit, AfterViewInit {

  @ViewChildren('input') vc;
  public searchBarActive: boolean
  public blurFlag: boolean

  constructor() { }

  ngOnInit() {
  }

  ngAfterViewInit() { }

  public toggleSearchBar(forceBlur?: boolean) {
    if(this.blurFlag) {
      this.blurFlag = false
      return
    }

    this.searchBarActive = !this.searchBarActive;

    if (this.searchBarActive) {
      this.vc.first.nativeElement.focus();
    }
  }

  public onSearchUnfocus() {
    console.log('unfocus');
    this.blurFlag = true
    this.searchBarActive = false
  }

}



